@keyframes moves-hand {
  0% {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(0, -50%)rotate(-90deg);
  }

  100% {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(0, -50%)rotate(270deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand, .stopwatch__seconds-hand {
  transform-origin: 0;
}

.stopwatch__minutes-hand {
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation-name: moves-hand;
  animation-duration: 3.6s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%)rotate(0);
}

.stopwatch__minutes-hand--speed-up {
  animation-name: moves-hand;
  animation-duration: 600s;
  animation-iteration-count: infinite;
}

.stopwatch__seconds-hand {
  background-color: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  animation-name: moves-hand;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%)rotate(0);
}

.stopwatch__seconds-hand--speed-up {
  animation-name: moves-hand;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(0);
}
